* {
  box-sizing: border-box;
  background-color: #f5f5f7;
}

.container {
  width: 100%;
  height: 100vh;
  position: relative;
  margin: 0;
  padding: 48px 0;
}

.header,
.footer {
  z-index: 90;
  width: 100%;
  height: 48px;
  background-color: #fff;
  border: 3px solid #141414;
  justify-content: flex-start;
  align-items: center;
  display: flex;
  position: absolute;
  overflow: hidden;
}

.header {
  top: 0;
}

.footer {
  z-index: 70;
  justify-content: flex-end;
  position: absolute;
  bottom: 0;
}

.main-wrapper {
  width: 100%;
  height: 100%;
  position: relative;
  background-color: #000000;
}

.inner-box {
  position: absolute;
  background-color: white;
  border: 3px solid black;
  display: flex;
  justify-content: center;
  font-size: 50px;
  align-items: center;
  background-color: #fff;
  color: #222;
  &:hover {
    background-color: #333;
    color: #fff;
  }

  transition:
    width 1s,
    height 1s,
    transform 1s;
}

.about {
  width: 60%;
  height: 40%;
  top: 0;
  left: 0;
  border-right-width: 0;
  border-top-width: 0;
}

.history {
  width: 40%;
  height: 60%;
  top: 0;
  right: 0;
  border-bottom-width: 0;
  border-top-width: 0;
}

.project {
  width: 40%;
  height: 60%;
  bottom: 0;
  left: 0;
  border-top-width: 0;
  border-bottom-width: 0;
}

.stack {
  width: 60%;
  height: 40%;
  bottom: 0;
  right: 0;
  border-left-width: 0;
  border-bottom-width: 0;
}
.inner-box {
  transition:
    background-color 0.5s ease,
    color 0.5s ease,
    width 0.8s,
    height 0.8s,
    transform 0.8s;
}

.logo {
  width: auto;
  max-width: 100%;
  height: auto;
  max-height: 100%;
}

.user-info {
  width: 20%;
  height: 20%;
  top: 50%;
  left: 50%;
  background-color: #000000;
  color: #000000;
  &:hover {
    background-color: #000000;
    color: #fff;
  }
  transform: translate(-50%, -50%);
}

/* contact가 확장될 때의 효과 */
.container[data-hovered="about"] .about {
  width: 65%;
  height: 50%;
}

/* contact가 확장될 때 다른 박스들의 크기 */
.container[data-hovered="about"] .history {
  width: 35%;
  height: 70%;
}

.container[data-hovered="about"] .project {
  width: 45%;
  height: 50%;
}

.container[data-hovered="about"] .stack {
  width: 55%;
  height: 30%;
}

.container[data-hovered="about"] .user-info {
  transform: translate(-25%, 0%);
}

/* history가 확장될 때의 효과 */
.container[data-hovered="history"] .history {
  width: 50%;
  height: 65%;
}

/* history가 확장될 때 다른 박스들의 크기 */
.container[data-hovered="history"] .about {
  width: 50%;
  height: 45%;
}

.container[data-hovered="history"] .project {
  width: 30%;
  height: 55%;
}

.container[data-hovered="history"] .stack {
  width: 70%;
  height: 35%;
}

.container[data-hovered="history"] .user-info {
  transform: translate(-100%, -25%);
}

/* project가 확장될 때의 효과 */
.container[data-hovered="project"] .project {
  width: 60%;
  height: 75%;
}

/* project가 확장될 때 다른 박스들의 크기 */
.container[data-hovered="project"] .about {
  width: 80%;
  height: 25%;
}

.container[data-hovered="project"] .history {
  width: 20%;
  height: 45%;
}

.container[data-hovered="project"] .stack {
  width: 40%;
  height: 55%;
}

.container[data-hovered="project"] .user-info {
  transform: translate(50%, -125%);
}

/* strength가 확장될 때의 효과 */
.container[data-hovered="stack"] .stack {
  width: 70%;
  height: 50%;
}

/* strength가 확장될 때 다른 박스들의 크기 */
.container[data-hovered="stack"] .about {
  width: 50%;
  height: 30%;
}

.container[data-hovered="stack"] .history {
  width: 50%;
  height: 50%;
}

.container[data-hovered="stack"] .project {
  width: 30%;
  height: 70%;
}

.container[data-hovered="stack"] .user-info {
  transform: translate(-100%, -100%);
}

/* userInfo가 확장될 때의 효과 */
.container[data-hovered="user-info"] .user-info {
  width: 40%;
  height: 40%;
}

/* userInfo가 확장될 때 다른 박스들의 크기 */
.container[data-hovered="user-info"] .about {
  width: 30%;
  height: 70%;
}

.container[data-hovered="user-info"] .history {
  width: 70%;
  height: 30%;
}

.container[data-hovered="user-info"] .project {
  width: 70%;
  height: 30%;
}

.container[data-hovered="user-info"] .stack {
  width: 30%;
  height: 70%;
}
